@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app__layout{box-sizing:border-box;background:#e3e9ff;height:100vh;display:flex;justify-content:center;align-items:center}.todolist__layout{width:90%;height:100%}.todolist__title{width:100%;height:50px;background:#af7eeb}.todolist__title-content{color:white;text-align:center;padding:4px 0 0;font-size:2.5em}.todolist__list{height:80%;overflow-y:scroll;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);background:white;margin:15px 0 0}.todolist__add{position:relative;text-align:center;margin:-25px 0 0}.todolist__add-button{font-size:22px;color:white;background:#af7eeb;border:none;border-radius:25px;width:180px;height:48px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.todolist__add-button:hover,.todolist__add-button:focus,.todolist__add-button:active{cursor:pointer;background:#895ebd}.todolist-item{display:flex;width:100%;height:22px;justify-content:space-evenly;align-items:center;margin:10px 0}.todolist-item__label{display:block;position:relative;margin:0 0 21px 0;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.todolist-item__label:hover input ~ .todolist-item__checkmark{background-color:#868686}.todolist-item__label .todolist-item__checkmark:after{left:50%;top:45%;transform:translate(-50%, -50%) rotateZ(45deg);width:3px;height:6px;border:solid white;border-width:0 3px 3px 0}.todolist-item__checkbox{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.todolist-item__checkbox:checked ~ .todolist-item__checkmark{background-color:#af7eeb}.todolist-item__checkbox:checked ~ .todolist-item__checkmark::after{display:block}.todolist-item__checkmark{position:absolute;top:0;left:0;height:18px;width:18px;border-radius:50%;background-color:#adacac}.todolist-item__checkmark::after{content:"";position:absolute;display:none}.todolist-item__form{border:none;width:70%;font-size:18px;text-align:center;font-style:italic;opacity:0.5}.todolist-item__remove{border:none;background:url(data:image/jpeg;base64,) no-repeat;background-size:cover;width:18px;height:18px;opacity:0.45}.todolist-item__remove:hover,.todolist-item__remove:focus{cursor:pointer;opacity:0.57}@media (min-width: 550px) and (max-width: 996px){.todolist__layout{width:100%;height:90%}.todolist__title{height:80px}.todolist__title-content{padding:10px 0 0;font-size:3em}.todolist__list{margin:25px 0 0}.todolist__add-button{font-size:32px;width:240px;height:62px}.todolist-item{height:36px}.todolist-item__label .todolist-item__checkmark:after{width:7px;height:13px}.todolist-item__checkmark{height:30px;width:30px}.todolist-item__form{width:75%;font-size:28px}.todolist-item__remove{width:30px;height:30px}}@media (min-width: 996px){.todolist__layout{width:840px;height:760px}.todolist__title{height:60px;margin-top:-80px}.todolist__title-content{padding:5px 0 0;font-size:2.8em}.todolist__list{height:700px;margin:18px 0 0}.todolist__add-button{font-size:28px;width:220px;height:52px}.todolist-item{height:28px}.todolist-item__label .todolist-item__checkmark:after{width:7px;height:13px}.todolist-item__checkmark{height:26px;width:26px}.todolist-item__form{width:75%;font-size:24px}.todolist-item__remove{width:26px;height:26px}}

